.section {
	padding: 50px 0 35px 0;
	position: relative;
	border-bottom: 1px solid $beige;
	@include breakpoint($ltmobile) {
		padding: 70px 0 45px 0;
	}
	&:not(:first-child)::after {
		content: "";
		color: #fff;
		display: block;
    position: absolute;
    bottom: -48px;
    left: 50%;
		transform: translate(-50%, -50%);
		-webkit-transform: translate(-50%, -50%);
		height: 48px;
		width: 48px;
		background-color: $beige;
		border-radius: 50%;
		z-index: 1;
    text-align: center;
    font-weight: bold;
    line-height: 3.2em;
		background-image: url(../images/arrow-down.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 32%;
	}
	&:last-child::after {
		display: none;
	}
	&:nth-last-child(2) {
		&::after {
			content: "";
			background-image: url(../images/tass.png);
			background-size: 64%;
			background-repeat: no-repeat;
			transform: translate(-50%, -50%) rotate(0);
			background-position-y: -27px;
			background-position-x: 10px;
			background-size: 26px;
			z-index: 10;
		}
	}
	&.image-section {
		z-index: 1;
		&::after {
			background-color: #fff;
			background-image: url(../images/arrow-down-grey.svg);
		}
		&:not(:first-child)::before {
			content: "";
			color: #fff;
			display: block;
			position: absolute;
			top: -2px;
			left: 50%;
			transform: translate(-50%, -50%);
			-webkit-transform: translate(-50%, -50%);
			height: 50px;
			width: 50px;
			background-color: #fff;
			border-radius: 50%;
			z-index: 1;
			text-align: center;
			font-weight: bold;
			line-height: 3.2em;
			background-image: url(../images/arrow-down-grey.svg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: 32%;
		}
	}
	h2 {
			margin: 0;
			@include breakpoint($ltpmobile) {
				padding-bottom: 1rem;
			}
	}
	.cta {
		// margin-top: 30px;
	}

	// Beige section

	&.beige {
		background-color: $beige;
    h2, h3, h4, h5, h6 {
      color: $dark-grey;
    }
		p {
			color: $grey;
		}

	}
}


.col-3 {
  margin-bottom: 20px;
	@include breakpoint($ltmobile) {
		margin-bottom: 20px;
		@include span(4 of 12);
		&:last-child {
			@include last
		}
	}
	@include breakpoint($ltmobile $ltpmobile) {

	}
}

.col-4 {
  margin-bottom: 20px;
	@include breakpoint($ltmobile $lttablet) {
		margin-bottom: 20px;
		@include span(6 of 12);
		&:nth-child(2n) {
			@include last
		}
	}
	@include breakpoint($lttablet) {
		margin-bottom: 20px;
		@include span(3 of 12);
		&:last-child {
			@include last
		}
	}
	@include breakpoint($ltmobile $ltpmobile) {

	}
}

.events, .press-release, .latest-reports {
	margin-top: 20px;

	.col-3 {
		p {
			margin: 0;
		}
	}
}

// First calander bg primary-color
.events {
	.col-3 {
		&:first-child {
			.calender {
				&::before {
					background-color: $primary-color;
				}
			}
		}
	}
}
