.form, .subscription-form {
  text-align: left;
  color: $grey;
  padding: 10px 10px 0 10px;

  fieldset {
    border: none;
    margin: 15px 0;
    padding: 0;

    @include breakpoint($lttablet) {
      margin: 20px 0 20px 0;
    }

    legend {
      font-weight: 700;
      font-size: 1.4rem;
      margin-bottom: 10px;

      @include breakpoint($lttablet) {
        font-size: 0.8rem;
        margin-bottom: 15px;
      }
    }

    input, label, select {
      font-weight: 700;
      font-size: 1.2rem;

      @include breakpoint($lttablet) {
        font-size: 0.7rem;
      }
    }

    label {
      display: inline-block;
      margin-bottom: 6px;
    }

    input[type='checkbox'] {
      appearance: none;
      background: transparent;
      border-radius: 0;
      width: 22px;
      height: 22px;
      border: none;
      z-index: 1;
      position: relative;
      opacity: 0;

      & + span {
        z-index: 0;
        position: relative;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 80%;
        height: 18px;
        width: 18px;
        margin-left: -18px;
        display: inline-block;
        background-color: #fff;

        & + label {
          top: -4px;
          position: relative;
          margin-left: 5px;
        }
      }
      &:checked {
        & + span {
          background-image: url(../images/checkbox.svg);
        }
      }
    }

    .select-wrap {
      display: inline-block;
      position: relative;
      background: #fff;
      width: 140px;
      margin-right: 5px;
      top: 0px;
      overflow: hidden;
      vertical-align: middle;

      &::before {
        content: "";
        display: block;
        position: absolute;
        right: 0;
        top: 0;
        background: $grey;
        width: 2.5rem;
        height: 100%;
        z-index: 0;
        background-image: url(../images/arrow-down.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 40%;

        @include breakpoint($lttablet) {
          width: 2rem;
        }
        @include breakpoint($ltdesktop) {
          width: 1.8rem;
        }
      }

      select {
        clear: left;
        border: none;
        background: transparent;
        border-radius: 0;
        appearance: none;
        width: 160px;
        padding: 8px 0px 8px 10px;
        position: relative;

        &::-ms-expand {
          display: none;
        }
      }
    }

    .select-days {
      margin: 0px 0 5px 0;

      @include breakpoint($ltpmobile) {
        display: inline-block;
        margin: -4px 0 5px 0;
        position: relative;
        top: -4px;
        left: 12px;
      }
    }

    input[type='text'], input[type='email'] {
      display: block;
      border-radius: 0;
      border: 0;
      padding: 10px 10px;
      @include breakpoint(0 $ltmobile) {
        width: 100%;
      }
    }
  }

  .contact-information {
    margin-bottom: 30px;

    label {
      margin: 5px 0;
    }

    input {
      width: 100%;
      margin-top: 10px;

      @include breakpoint($ltpmobile) {
        @include span(4 of 12);
        margin-top: 0;

        &:nth-child(3n) {
          @include last
        }

      }

    }
  }

  p {
    max-width: none;
    .cta {
      margin: 0;
    }
  }

}
