.leovegas-icons {
  width: 160px;
  max-width: 90%;
  position: relative;
  display: block;
  margin: 10px auto 0px auto;
  background-color: transparent;
  @include breakpoint($lttablet) {
    max-width: 100%;
  }
  &::before {
    content: "";
    position: absolute;
    background-color: $beige;
    width: 100%;
    max-width: 100%;
    padding-bottom: 100%;
    border-radius: 50%;
    margin: 0 auto;
    display: block;
    transition: all 0.25s ease;
    position:relative;
  }
  &.white {
    &::before {
      background: #fff;
    }
  }
  &::after {
    bottom: 0;
    display: block;
    top: 50%;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease;
    height: 100%;
    width: 100%;
    content: "";
    background-position: center;
    background-repeat: no-repeat;
    background-size: 45%;
  }
  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    transition: transform 0.3s ease;
    width: 44%;
    height: auto;

  }
  &.press-release {
    &::after {
      background-image: url(../images/icons/press-release.svg);
    }
    &::before {
      background-color: $primary-color;
      @include breakpoint($ltmobile) {
        background-color: $beige;
      }
    }
  }
  &.corporate-governance {
    &::after {
      background-image: url(../images/icons/corporate-governance.svg);
    }
  }
  &.code-of-conduct {
    &::after {
      background-image: url(../images/icons/code-of-conduct.svg);
    }
  }
  &.insider {
    &::after {
      background-image: url(../images/icons/insider.svg);
    }
  }
  &.calender {
    &::after {
      background-position-y: 47%;
      background-size: 48%;
      background-image: url(../images/icons/calender.svg);
    }
    time {
      position: absolute;
      left: 50%;
      top: 56%;
      transition: transform 0.3s ease;
      transform: translate(-50%, -50%) scale(1);
      z-index: 1;
      color: inherit;
      font-size: 1.9em;
      font-weight: 700;
      font-style: normal;
      line-height: 0.9em;
      color: $grey;
      @include breakpoint($ltpmobile) {
          // font-size: 1.4em;
      }
      > span {
        font-size: 90%;
        display: block;
        font-weight: normal;
      }
    }
  }
  // The report icon needs to be in the view in an <svg> tag
  &.report {
    svg {
      .stroke-color {
        stroke: $primary-color;
        transition: stroke 0.3s ease;
        @include breakpoint($ltmobile) {
          stroke: $dark-grey;
        }
      }
      .fill-color {
        fill: $primary-color;
        transition: fill 0.3s ease;
        @include breakpoint($ltmobile) {
          fill: $dark-grey;
        }
      }
    }
  }
  &.sutainability {
    &::after {
      content: url(../images/icons/sutainability.svg);
    }
  }

  // Hover effects

  &:hover {
    &::before {
      background-color: $primary-color;
    }
    &::after, time {
      transform: translate(-50%, -50%) scale(1.15);
      path {

      }
    }
    svg {
      transform: translate(-50%, -50%) scale(1.15);
      .stroke-color {
        stroke: $primary-color;
      }
      .fill-color {
        fill: $primary-color;
      }
    }
    .quarter {
      transform: scale(1.15);
    }
  }
  &.white {
    &:hover {
      &::before {
        background-color: #fff!important;
        color: $primary-color;
      }
    }
  }
  &.animate-content {
    svg {
      path:not(:nth-child(2)) {
        transition: fill 0.3s ease, transform 0.3s ease;
      }
    }
    &::after {
      background-image: none;
    }
    &:hover {
      &::before {
        background-color: inherit;
      }
      svg {
        path:not(:nth-child(2)):not(.no-animation) {
          fill: $primary-color;
        }
      }
    }
  }
}

.small-icon {
  max-width: 36px;
}

.quarter {
  transition: all 0.3s ease;
  transform: scale(1);
  position: absolute;
  top: 15%;
  right: 21%;
  z-index: 1;
  font-weight: 700;
  width: 19%;
  height: 19%;
  background: $primary-color;
  border-radius: 50%;
  color: #fff;
  font-size: 1rem;
  line-height: 2.5rem;
  @include breakpoint($lttablet) {
    font-size: 0.8rem;
    line-height: 1.95rem;
  }
  @include breakpoint($ltdesktop) {
    font-size: 0.7rem;
    line-height: 1.75rem;
  }
}
