.table-list {
  width: 100%;
  text-align: left;
  margin: 20px 0 40px 0;
  font-size: 0.8rem;
  @include breakpoint($ltmobile) {
    font-size: 0.9em;
  }
  tr {
    border-bottom: 1px solid $beige;
  }

  th, td {
    padding: 15px 0;
  }

  th {
    font-weight: 700;
  }
  .positive {
    color: $success;
  }
  &.table-col-2 {
    @include breakpoint($ltmobile) {
      th, th {
        width: 50%;
      }
    }
  }
}
