footer {
  position: relative;
  background: $dark-grey;
  address {
    width: 100%;
    color: $light-grey;
    z-index: 1;
    font-size: 0.9rem;
    font-style: normal;
    padding: 8px 10px 10px 10px;
    height: 54px;
    line-height: 1.3em;

    @include breakpoint($lttablet) {
      height: 40px;
      font-size: 0.7em;
      padding: 10px 10px 10px 10px;
    }

    h3 {
      font-weight: bold;
      display: inline-block;
      font-family: $main-font;
      margin: 0;
      color: $light-grey;
    }

    .textwidget {
      display: inline-block;
      width: 100%;
      text-align: center;

      &::before {
        content: " ";
        background-image: url(../images/tass.png);
        background-size: 100%;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 5px -4px 0;
        opacity: 0.5;
      }
    }
  }
}
