.dropdown {
  background: #fff;
  position: relative;
  width: 100%;
  margin: 30px auto 10px auto;
  color: $grey;
  font-weight: 700;
  text-align: left;
  font-size: 1rem;
  clear: both;

  @include breakpoint($ltpmobile) {
    max-width: 360px;
  }
  @include breakpoint($lttablet) {
    font-size: 0.7rem;
  }

  .label {
    padding: 14px 15px;
    cursor: pointer;
    z-index: 3;
    background: #fff;
    position: relative;
    display: block;
    &:hover {
      text-decoration: none;
    }

    .arrow {
      position: absolute;
      display: block;
      right: 0;
      top: 0;
      background-color: $primary-color;
      width: 3.2rem;
      height: 100%;
      overflow: hidden;
      @include breakpoint($lttablet) {
        width: 2.5rem;
      }
      @include breakpoint($ltdesktop) {
        width: 2.2rem;
      }
      img {
        transition: transform 0.2s ease-in-out;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

  }

  ul {
    position: absolute;
    transform: translateY(-38px);
    transition: transform 0.3s ease, opacity 0.4s 0.1s ease, max-height 0.3s 0.1s ease;
    // opacity: 0;
    overflow: scroll;
    background: #fff;
    z-index: 2;
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0px 0;
    max-height: 0;
    overflow: hidden;
    li {
      padding: 12px 15px;
      border: 1px solid transparent;
      transition: border-color 0.3s ease;
      cursor: pointer;
      &:hover {
        border-color: $grey;
      }
    }
  }

  &.active {
    // overflow: visible;
    .label {
        img {
          transform: translate(-58%, -50%) rotate(180deg);
      }
    }
    ul {
      transform: translateY(0%);
      opacity: 1;
      max-height: 500px;
    }
  }

  &.beige {
      .label, ul {
        background-color: $beige;
    }
  }
}
