.single-contact {
  .img-frame {
    max-width: 160px;
    margin-top: 30px;

    @include breakpoint($ltpmobile) {
      float: right;
      margin: 5px 25px 0 0;
      max-width: 134px;
    }
  }
  .contact-info {
    display: inline-block;
    text-align: left;
    width: 100%;

    @include breakpoint($ltpmobile) {
      width: auto;
      margin-top: 20px;
      > div {
        float: left;
        margin: 0 auto;
      }
    }
    @include breakpoint($lttablet) {
      margin: 40px 0;
    }
    p {
      margin-top: 5px;
    }
  }
}
