// Susy setup


body:not(#tinymce) {
  $susy: (
    columns: 12,
    global-box-sizing: border-box,
    debug: (
      image: show-columns,
      output: overlay,
      toggle: top right
    )
  );
}

body#tinymce {
  padding-top: 0 !important;
  $susy: (
    columns: 12,
    global-box-sizing: border-box,
  );
}

@include border-box-sizing;

.container {
	@include container(center 12 900px);
	padding: 0 10px;
  @include breakpoint($ltmobile) {
    padding: 0 20px;
  }
}

.container-large {
	@include container(center 12 1040px);
	padding: 0 10px;
  @include breakpoint($ltmobile) {
    padding: 0 20px;
  }

  p, h2 {
    max-width: 860px;
    margin-left: auto!important;
    margin-right: auto!important;
  }
}

.container-wide {
  @include container(center 12 1280px);
}

.container-center {
	@include container(center 12);
}

