.financial-reports {
  svg {
    // background: #fff;
    border-radius: 2px;
  }
  .cta {
    margin: 20px;
  }
  h4 {
    color: $grey;
  }

}

.quarterly-reports {
  max-height: 0;
  overflow: hidden;
  transition: transform 0.3s ease, opacity 0.2s ease, max-height 0s 0.3s ease;
  opacity: 0;
  transform: translateY(60%);
  &.visible {
    transform: translateY(0);
    transition: transform 0.3s 0.3s ease, opacity 0.2s 0.3s ease, max-height 0s 0.3s ease;
    opacity: 1;
    max-height: 2000px;
  }
}
