.hero {
	height: 400px;
	background: $primary-color;
	background: linear-gradient($primary-color, darken($primary-color, 10%));
	background-size: auto 130%;
	background-size: auto calc(100% + 150px);
	background-position: center;
	background-repeat: no-repeat;
	position: relative;
	will-change: transform;

	@include breakpoint($lttablet) {
		height: 440px;
	}

	@include breakpoint($ltdesktophd) {
		background-size: cover;
	}

		.hero-filter {
			content: "";
			display: block;
			position: absolute;
			height: 100%;
			width: 100%;
			background-image: url(../images/header-filter-left.png);
			background-size: cover;
	    background-position: center;
	    left: 0;
	    top: 0;
	    /* background-image: url(http://lorempixel.com/output/business-q-c-1202-480-9.jpg); */
	    background-blend-mode: multiply;
	    background-color: rgba(238, 22, 0, 0.01);
	    -webkit-filter: saturate(1.2);
	    opacity: 0.9;
		}
		.container {
			position: relative;
			height: 100%;
		}
		.hero-content {
			position: absolute;
			top: 50%;
			left: 50%;
			padding: 0 20px;
			transform: translate(-50%, -50%);
			-webkit-transform: translate(-50%, -50%);
			width: 100%;
			text-align: center;
			color: #fff;
	    max-width: 820px;
		}
		h2 {
			margin: 0;
			color: #fff;
			font-size: 3.8em;
			b {
				color: $primary-color;
				font-weight: normal;
			}
		}
}
