.media-gallery-section {
  padding-top: 0;
  padding-bottom: 0;
}

.media-filter {
  input {
    margin: 10px 0 0;
    padding: 5px;
    font-size: 1rem;
    width: 80%;
    &:focus {
      outline: none;
    }

    @include breakpoint($lttablet) {
      padding: 10px;
      font-size: .6rem;
      width: 40%;
    }
  }
}

.media-gallery {
  .media-image {
    overflow: hidden;
    @include span(100% no-gutters);

    position: relative;
    &:after {
      content: "";
      padding-bottom: 70%;
      display: block;
    }
    &:hover {
      .media-hover {
        opacity: 1;
      }
      .image-container {
        transform: scale(1.25);
      }
    }

    .image-container {
      background-size: cover;
      background-position: 50% 50%;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      transform: scale(1);
      transition: transform .5s ease-out;
    }

    .media-hover {
      z-index: 0;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0,0,0,0.5);
      opacity: 1;
      transition: opacity .3s ease-out;
      .inner {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        transform: translate(0, -50%);
        h3 {
          font-family: "Gotham A","Gotham B",sans-serif;
          margin-top: 0;
          font-weight: bold;
          font-size: 1.5rem;
        }
        h3, p {
          color: #fff;
          margin: 3px 0;
        }
        a {
          margin: 5px 0 0;
        }
      }

    }
  }
}


@include breakpoint($lttablet) {
  .media-gallery {
    .media-image {
      .media-hover {
        opacity: 0;
      }
      @include span(50% no-gutters);
      .media-hover .inner {
        h3 {
          font-size: 1rem;
        }
        p {
          font-size: 1rem;
        }
      }
    }
  }
}
