.report-list {
  list-style: none;
  margin: 40px 0;
  padding: 20px 20px;
  text-align: left;
  background-color: #fff;

  &.border {
    border: 1px solid $beige;
  }

  @include breakpoint($lttablet) {
    text-align: left;
    padding: 20px 50px;
    margin: 60px 0;
  }
  @include breakpoint($ltdesktop) {
    padding: 20px 50px;
  }

  li {
    padding: 30px 0;
    margin: 0;
    background-color: #fff;
    border-bottom: 1px solid $beige;
    text-align: center;

    @include breakpoint($lttablet) {
      text-align: left;
    }

    &:last-child {
      border: none;
    }

    @include breakpoint($ltpmobile $lttablet) {
      @include span(50%);
      margin: 0;
      position: relative;
      &:nth-child(2n) {
        @include last;
      }
      &:nth-child(odd):not(:first-child) {
        clear: left;
      }
      &:nth-last-child(2):not(:nth-child(even)) {
        border: none;
      }
    }
    svg, img {
      margin: 10px auto;
      max-width: 50px;
      height: auto;
      background-color: #fff;
      display: block;
      padding: 0;

      @include breakpoint($lttablet) {
        margin: 0;
        max-width: 36px;
        max-height: 54px;
        display: inline-block;
        vertical-align: middle;
      }
    }
    .cta {
      clear: both;
      margin: 20px auto 10px auto;
      @include breakpoint($lttablet) {
        float: right;
        margin-top: 5px;
        vertical-align: middle;
      }

    }
    .list-text {
      font-size: 1rem;
      font-weight: 700;
      display: block;
      color: $grey;
      span {
        display: block;
        font-weight: normal;
        font-style: italic;
        margin-top: 3px;
      }
      @include breakpoint($ltpmobile $lttablet) {
        min-height: 56px;
        padding: 0 5px;
      }
      @include breakpoint($lttablet) {
        font-size: 0.8rem;
        margin-left: 10px;
        max-width: 70%;
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
}

.report-list-filter {
  li {
    transition: padding 0.3s 0s ease, max-height 0.3s 0s ease, opacity 0.3s 0s ease;
    max-height: 400px;
    padding: 30px 0;
    border-width: 1px;
    opacity: 1;

    @include breakpoint($lttablet) {
      max-height: 200px;
    }
    &.anim-fast {
      transition: padding 0.3s ease, max-height 0.3s ease, opacity 0.3s ease;
    }
    &.hidden {
      max-height: 0;
      padding: 0;
      overflow: hidden;
      border-width: 0;
      opacity: 0;
      transition: padding 0.3s ease, max-height 0.3s ease, opacity 0.3s ease;
    }
  }
}

.no-border {
  border: none!important;
}
