header {
  position: fixed;
  padding: 6px 0;
  top: 0;
  left: 0;
  width: 100%;
  height: $header-height;
  z-index: 11;
  background-color: #fff;
  transition: transform 0.4s ease;
  border-bottom: 1px solid #eee;
  will-change: transform;

  .container {
    padding: 0 12px;
  }
  &::after {
    content: "";
    display: block;
    height: 100%;
    position: static;
  }
}

.brand {
  float: left;
  width: 160px;
  margin-top: 4px;
  img {
    vertical-align:top;
    width: 160px;
  }
}


.nav-primary {
  z-index: 12;
}

// ----- Menu

nav {
  color: $primary-color;
  z-index: 2;
  position: fixed;
  transform: translateX(100%);
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  overflow: scroll;
  padding-top: 5px;
  background: #fff;
  font-size: 1.2rem;

  @include breakpoint($ltpmobile) {
    font-size: 1rem;
  }

  .hamburger {
    display: none;
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    @include breakpoint($ltpmobile) {
      display: block;
    }
  }

  .main-menu {
    padding: 0 10px;
    background: rgba(#fff, 1);
    transition: all 0.4s ease;
    overflow: hidden;
    width: 100%;

    .brand {
      margin-top: 4px;
      @include breakpoint($ltpmobile) {
        margin: 5px 15px;
      }
    }

    > div > ul {
      clear: both;
      font-family: $header-font;
      margin: 0;
      padding: 10px 0 0 0px;
      list-style: none;
      margin: 0;
      top: 0px;

      @include breakpoint($ltpmobile) {
        width:auto;
        max-width: $container-max-width;
        margin: 0 auto;
        padding-top: 40px;
      }

      > li {
        margin: 0 10px 0 0 ;
        padding: 12px 0;
        cursor: pointer;
        border-bottom: 1px solid $beige;
        width: 80%;
        width: calc(100% - 50px);
        position: relative;
        font-size: 1.6rem;

        @include breakpoint($ltpmobile) {
          width: 100%;
        }

        a {
          color: $primary-color;
          text-decoration: none;
        }

        &::after {
          font-family: $main-font;
          content: "";
          display: block;
          position: absolute;
          right: 5px;
          top: 11px;
          color: $light-grey;
          font-size: 0.7em;
          font-weight: bold;
          text-shadow: 1px 0px 0 $light-grey;
          transition: transform 0.3s ease;
          background-image: url(../images/arrow-down-grey.svg);
          background-position: center;
          background-repeat: no-repeat;
          height: 20px;
          width: 20px;

          @include breakpoint($ltpmobile) {
            display: none;
          }
        }
        &.active::after {
          -webkit-transform: rotate(180deg)
        }
        ul {
          max-height: 0;
          overflow: hidden;
          margin: 0;
          padding: 0;
          list-style: none;
          transition: max-height 0.3s ease;
          font-family: $main-font;
          font-weight: bold;
          font-size: 1.2rem;

          @include breakpoint($ltdesktop) {
            font-size: 0.9rem;
          }

          @include breakpoint($ltpmobile) {
            max-height: none;
          }

          li {
            padding: 15px 0 0 0;
            margin-left: 0;
            width: 100%;
            border-bottom: none;
            a {
              color: $light-grey;
            }
            &:last-child {
              padding-bottom: 10px;
            }

            @include breakpoint($ltpmobile) {
              max-height: none;
              // @include span(25%);
              @include span(4 of 12);
              &:nth-child(3n+3) {
                @include last
              }
            }

          }
        }
        &.active {
          ul {
            max-height: 500px;
          }
        }
      }
    }
  }
}

//  Dark overlay
.wrap {
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    opacity: 0;
    background: rgba(#000, .5);
    transition: height 0.3s ease, width 0.3s ease, opacity 0.5s ease;
    z-index: 1;
    @include breakpoint($ltpmobile) {
      display: none;
    }
  }
}

// Menu slide function and padding for fixed menu
body:not(#tinymce) {
  position: relative;
  padding-top: $header-height;

  nav, .wrap, footer, header {
    transition: transform 0.3s ease;
  }
  nav {
    transition: transform 0.4s ease;
  }

  &.menu-active {

    nav {
      transform: translateX(50px);
    }

    header, .wrap, footer {
      transform: translateX(calc(-100% + 50px));
    }

    // Larger than mobile menu behaviour
    @include breakpoint($ltpmobile) {
      header, .wrap, footer {
        transform: initial;
      }
      nav {
        transform: translateX(0);
        top: 0;
      }
    }
    .wrap {
      &:after {
        @include breakpoint(0 $ltpmobile) {
          opacity: 1;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}


// ----- Hamburger
.hamburger {
  color: $primary-color;
  cursor: pointer;
  float: right;
  margin-top: 14px;
  font-weight: 700;
  @include breakpoint($ltdesktop) {
    font-size: 0.8em;
  }
}

.hamburger-icon {
  margin-left: 6px;
  display: inline-block;
  position: relative;
  width: 28px;
  max-width: 28px;
  height: 4px;
  background-color: $primary-color;
  border-radius: 2px;
  top: -0.25em;
  transition: all 0.3s ease;
  &::after, &::before {
    transition: all 0.3s ease;
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    max-width: 100%;
    height: 100%;
    background-color: inherit;
    border-radius: inherit;
  }
  &::after {
    top: 8px;
  }
  &::before {
    top: -8px;
  }

  @include breakpoint($ltdesktop) {
    height: 2px;
    width: 24px;
    max-width: 24px;

    &::after {
      top: 6px;
    }
    &::before {
      top: -6px;
    }
  }
}

body.menu-active {
  .hamburger-icon {
    transform: rotate(45deg);
    &::before {
      transform: rotate(-90deg);
      top: 0;
    }
    &::after {
      max-width: 0;
      transform: translateX(100%);
    }
  }
}


// ----- Language switch
header .language-switcher {
  position: relative;
  float: right;
  height: 40px;
  width: 40px;
  margin: 0px 0 0 15px;
  padding: 12px 0;
  cursor: pointer;
  font-size: 1rem;
  color: $light-grey;
  transition: background-color 0.3 ease;
  font-weight: 700;
  display: none;
  z-index: 3;

  @include breakpoint($ltpmobile) {
    display: block;
  }
  @include breakpoint($lttablet) {
    width: 42px;
    height: 43px;
    font-size: 0.9rem;
  }
  @include breakpoint($ltdesktop) {
    width: 42px;
    height: 44px;
    padding: 13px 0;
    font-size: 0.7rem;
  }

  &::after {
    content: "";

    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 20px;
    height:100%;
    background-image: url(../images/arrow-down-grey.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
  ul {
    background-color: $beige;
    margin: 10px 0 0 0;
    font-size: 0.7rem;
    font-weight: normal;
    list-style: none;
    // border: 1px solid transparent;
    max-height: 0;
    overflow: hidden;
    position: absolute;
    right: 0;
    padding: 0;
    transition: max-height 0.3s ease, background-color 0.3s ease;
    z-index: 5;

    li {
        font-size: 1rem;
        padding: 5px 10px;;
        text-align: right;
        transition: color 0.3s ease;

        @include breakpoint($lttablet) {
          font-size: 0.8rem;
        }
        @include breakpoint($ltdesktop) {
          font-size: 0.7rem;
        }

        &:last-child {
          padding-bottom: 10px;
        }
        &:first-child {
          padding-top: 10px;
        }
        &:hover {
          color: $dark-grey;
        }
    }
  }
  &:hover {

    ul {
      max-height: 300px;
      border-color: $beige;
    }
  }
}

.language-switcher-mobile {
  @include breakpoint($ltpmobile) {
    display: none;
  }
}
.banner .container-wide {
  padding: 0 10px;
}
