// Search form
.search-form {
  @extend .form-inline;
}
.search-form label {
  font-weight: normal;
  @extend .form-group;
}
.search-form .search-field {
  @extend .form-control;
}
.search-form .search-submit {
  @extend .btn;
  @extend .btn-default;
}

.single-select.form {
  padding: 10px 0 10px 0;
  text-align: center;
  fieldset {
    .select-wrap {
      width: 100%;
      text-align: left;
      &::before {
        width: 3rem;
        @include breakpoint($lttablet) {
          width: 2.6rem;
        }
        @include breakpoint($ltdesktop) {
          width: 2.1rem;
        }
      }
      &.beige {
        background: $beige;
        &::before {
          background-color: $primary-color;
        }
      }
      @include breakpoint($ltpmobile) {
        width: 320px;
      }
      select {
        width: 100%;
        padding: 12px 0 11px 15px;
        @include breakpoint($ltpmobile) {
          width: 340px;
        }
      }
    }
  }
}
