.business-management {
  position: relative;

  .swiper-container {
    text-align: left;
    max-width: 860px;
    margin: 40px auto 0 auto;

    .swiper-wrapper {
      padding-bottom: 100px;

      @include breakpoint($ltpmobile) {
        padding-bottom: 140px;
      }

      @include breakpoint($lttablet) {
        padding-bottom: 150px;
      }
      @include breakpoint($ltdesktop) {
        padding-bottom: 170px;
      }
    }

    .swiper-slide {
      .img-frame {
        margin: 40px auto;
        @include breakpoint($lttablet) {
          float: left;
          width: 20%;
          margin-right: 5%;
        }
      }

      .slider-text {
        p {
          margin-top: 8px;
          font-size: 1rem;
          b {
            font-weight: normal;
            color: $dark-grey;
          }
        }

        @include breakpoint($lttablet) {
          float: left;
          width: 75%;

          h4 {
            font-size: 1rem;
          }
          p {
            font-size: 0.8rem;
            line-height: 1.3rem;
          }
        }
      }
    }
    .swiper-pagination {
      @include breakpoint($lttablet) {
      text-align: left;
      }
      @include breakpoint($lttablet) {
        margin-left: 25%;
      }

      ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
          display: inline-block;
          width: 46px;
          height: auto;
          background-color: transparent;
          opacity: 1;
          font-size: 0.8rem;

          @include breakpoint($lttablet) {
            width: 60px;
            margin: 0 10px;
            font-size: 1rem;
          }

          .img-frame {
            margin-left: 0;
            margin-right: 0;
            width: 100%;
            max-width: 100%;
            &:after {
              transition: background-color 0.3s ease;
            }
          }

          .pagination-text {
            opacity: 0;
            width: 120px;
            margin-left: -37px;
            transition: opacity 0.3s ease;
            display: none;
            text-align: center;

            @include breakpoint($ltpmobile) {
              display: block;
            }
            @include breakpoint($lttablet) {
              margin-left: -30px;
            }
          }

          h4 {
            margin-top: 0;
            padding-top: 0;
            @include breakpoint($ltpmobile) {
              font-size: 1rem;
            }
            @include breakpoint($lttablet) {
              font-size: 0.8rem;
            }
            @include breakpoint($ltdesktop) {
              font-size: 0.7rem;
            }
          }

          .work-title {
            color: $grey;
            // font-family: $main-font;
            margin: 4px 0 0 0;
            font-size: 0.8rem;
            @include breakpoint($ltpmobile) {
              font-size: 0.9rem;
              line-height: 1.3rem;
            }
            @include breakpoint($lttablet) {
              font-size: 0.7rem;
              line-height: 1rem;
            }
            @include breakpoint($ltdesktop) {
              font-size: 0.6rem;
              line-height: 0.8rem;
            }
          }

          &:hover {
            .img-frame {
              &:after {
                background-color: $primary-color;
              }
            }
            .pagination-text {
              opacity: 1;
            }
          }
          &.swiper-pagination-bullet-active {
            .img-frame {
              &:after {
                background-color: $primary-color;
              }
            }
          }
        }
      }
      .img-frame {
        max-width: 50px;
      }
    }
  }

  .slider-nav {
    width: 60px;
    height: 60px;
    position: absolute;
    top: 45%;
    border-radius: 50%;
    background-color: $beige;
    background-image: url(../images/arrow-down.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30%;
    transition: background-color 0.3s ease;
    cursor: pointer;
    display: none;

    @include breakpoint($ltdesktop) {
      display: block;
    }

    &:hover {
      background-color: $primary-color;
    }

    &.prev-slide {
      left: 0px;
      transform: translateY(-50%) rotate(90deg);
    }
    &.next-slide {
      right: 0px;
      transform: translateY(-50%) rotate(-90deg);
    }
  }
}
