.cta {
	position: relative;
	margin: 10px 0;
	font-weight: 700;
	font-size: 1.3rem;
	background: transparent;
	border: none;
	color: #fff;
	// transition: color 0.1s ease-in-out;
	z-index: 1;
	padding: 7px 40px 8px 40px;
	display: inline-block;
	overflow: hidden;

	background-color: $primary-color;
	border-radius: 25px;
	transition: color 0.1s ease-in-out, background 0.3s ease-in-out;
	border-style: solid;
	border-width: 2px;
	border-color: $primary-color;

	&::before {
		// content: "";
		// display: block;
		// position: absolute;
		// width: 100%;
		// width: calc(100% - 1px);
		// height: 100%;
		// height: calc(100% - 1px);
		// left: 0;
		// top: 0;
		// z-index: -1;
		// background-color: $primary-color;
		// padding: 0px 40px;
		// border-radius: 25px;
		// transition: background 0.3s ease-in-out;
		// border-style: solid;
		// border-width: 2px;
		// border-color: $primary-color;
	}
	span {
		display: block;
		backface-visibility: hidden;
	}
	@include breakpoint($ltmobile) {
		font-size: 1rem;
		margin: 40px 0 30px 0;
	}
	// @include breakpoint($lttablet) {
	// 	font-size: 0.8rem;
	// 	padding: 7px 40px;
	// }
	@include breakpoint($ltdesktop) {
		// font-size: 0.7rem;
		font-size: 0.8rem;
		padding: 6px 40px;
	}
	a {
		color: inherit;
	}
	&:hover {
		color: $primary-color;
		text-decoration: none;
	}
	&.light-grey {
		color: #fff;
		// &::before {
			background-color: $grey;
			border-color: $grey;
		// }
		&:hover {
			color: $grey;
		}
	}
	&.dark-grey {
		color: #fff;
		// &::before {
			background-color: $dark-grey;
			border-color: $dark-grey;
		// }
		&:hover {
			color: $dark-grey;
		}
	}
	&.beige {
		color: $grey;
		// &::before {
			background-color: $beige;
			border-color: $beige;
		// }
		&:hover {
			color: $dark-grey;
		}
	}
	&.white {
		color: $grey;
		// &::before {
			background-color: #fff;
			border-color: #fff;
		// }
		&:hover {
			color: $dark-grey;
		}
	}

	// Hover animation
	&:hover {
		// span {
		// 	animation: button-span-out 0.3s ease-in-out;
		// }
		background: transparent;
		&::before {
		// transform: rotatex(180deg);
		background: transparent;
		}
	}
}

