@font-face {
	font-family: 'LasVegas-Jakpot';
	src: url('../fonts/LasVegasOT-Jackpot.otf') format("opentype");
}

html {
	font-family: $main-font;
	color: $light-grey;
  -webkit-font-smoothing: antialiased;
	font-size: 13px;
	@include breakpoint($lttablet) {
		font-size: 15px;
	}
	@include breakpoint($ltdesktop) {
		font-size: 17px;
	}
}

h2.center {
  text-align: center;
}

h1, h2, .title {
	color: $primary-color;
	font-weight: 100;
	font-family:  $header-font;
	line-height: 1em;
}

h4, h5, h6, h3 {
	font-family: $main-font;
	font-weight: 700;
	color: $primary-color;
	line-height: 1em;
}

h1 {
  font-size: 3.7rem;
	@include breakpoint($lttablet) {
		font-size: 4.1rem;
	}
}

h2 {
	font-size: 3.5rem;
	@include breakpoint($lttablet) {
		font-size: 3.9rem;
	}
}

h3 {
	font-size: 1.5rem;
	padding-top: 15px;
	margin: 0 0 5px 0;
	@include breakpoint($lttablet) {
		font-size: 1rem;
	}
}

h4 {
	font-size: 1.2rem;
	padding-top: 15px;
	margin: 0 0 5px 0;
	@include breakpoint($ltdesktop) {
		font-size: 0.8rem;
	}
}

p {
	margin: 15px 0px 15px 0;
	font-size: 1rem;
	letter-spacing: 0.03em;
	line-height: 1.5em;
	clear: both;
	@include breakpoint($lttablet) {
		margin: 20px 0px 20px 0;
	}
	@include breakpoint($ltdesktop) {
		font-size: 0.9rem;
	}
}

.preamble {
	font-size: 1.3rem;
	line-height: 1.4em;
	@include breakpoint($lttablet) {
		// font-size: 1.3rem;
	}
}

blockquote {
	width: 100%;
	color: $primary-color;
	display: inline-block;
	float: left;
	margin: 0;
	padding: 5px 5px 5px 5px;
	@include breakpoint($ltpmobile) {
		width: 50%;
		padding: 5px 25px 5px 25px;
	}
}
body .main blockquote p {
	color: $primary-color;
	font-size: 2.8rem;
	font-weight: bold;
	padding: 5px 20px 25px;
	line-height: 1em;
	@include breakpoint($ltpmobile) {
		font-size: 2.5rem;
		line-height: 1em;
	}
}
blockquote + p {
	width: 100%;
	display: inline-block;
	margin-left: 0;
	margin-right: 0;
	@include breakpoint($ltpmobile) {
			width: 50%;
	}
}

time {
	margin: 0;
	font-style: italic;
  display: block;
}

.small-text {
	font-size: 1.2rem;
	@include breakpoint($ltdesktop) {
		font-size: 0.8rem;
	}
}

.text-center {
	text-align: center;
}

a {
	color: inherit;
	text-decoration: none;
	&:hover {
		color: inherit;
		text-decoration: underline;
	}
}

// Large quotes
h1 {
    position: relative;
    display: inline-block;
		left: 50%;
		transform: translateX(-50%);
    z-index: 1;
    color: #fff;
    padding: 20px;
    margin: 0;
}

h1:before {
    content: "";
    display: inline-block;
    width: 100%;
    position: absolute;
    min-height: 100%;
    background-color: $primary-color;
    z-index: -1;
    left: 0;
    transform: matrix(1, -0.01, 0.25, 1, 0, 1);
    top: 0;
}
