.map-contact {
  background: $primary-color;
  padding: 10px 0 10px 0;
  color: #fff;
  position: relative;
  border-bottom: 0!important;

  @include breakpoint($lttablet) {
    padding: 60px 0 100px 0!important;
  }
  @include breakpoint($ltdesktop) {
    padding: 80px 0 120px 0!important;
  }

  .contact-logo {
    float: left;
    left: 50%;
    transform: translateX(-50%);
    max-width: 100%;
    background: $primary-color;
    z-index: 1;
    position: relative;
    padding: 20px 20px 0 20px;
    margin: 0 auto;
    img {
      width: 190px;
      max-width: 100%;
      @include breakpoint($lttablet) {
        width: 98%;
      }
    }

    @include breakpoint($lttablet) {
      margin: 0;
      left: 0;
      transform: translateX(0);
      width: 280px;
    }
    @include breakpoint($ltdesktop) {
      width: 330px;
    }
  }

  address {
    float: left;
    clear: left;
    left: 50%;
    transform: translateX(-50%);
    max-width: 100%;
    padding: 0;
    background: $primary-color;
    margin: 0;
    z-index: 1;
    position: relative;
    text-align: left;
    display: block;
    margin: 0 auto;
    font-size: 0.9rem;
    font-style: normal;
    line-height: 1.4em;

    padding: 8px 20px 40px 20px;

    @include breakpoint($lttablet) {
      margin: 0;
      left: 0;
      transform: translateX(0);
      width: 280px;
      font-size: 0.9rem;
    }
    @include breakpoint($ltdesktop) {
      font-size: 0.75rem;
      width: 330px;
    }

    h3 {
      margin: 0;
      padding: 20px 0 10px 0;
      color: #fff;
      font-family: $main-font;
      font-weight: 700;
      font-style: normal;
    }
  }

  .map-button {
    text-align: center;
    margin: 0px 0 10px 0;
    clear: both;
    .cta {
      display: inline-block;
      clear: both;
      margin: 0 auto 8px auto;
      font-size: 1.3rem;
    }
    p {
      margin: 0;
      font-size: 0.8rem;
    }
    @include breakpoint($lttablet) {
      display: none;
    }
  }
}

#google-map {
  display: none;
  height: 100%;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.54);
  z-index: 0;
  @include breakpoint($lttablet) {
    display: block;
  }
}
