.img-frame {
  max-width: 100%;
  width: 180px;
  margin: 15% auto 15px auto;
  position: relative;
  &::after, span {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 0;
    background: $beige;
    padding-bottom: 100%;
    display: block;
    border-radius: 200px;
    overflow-y: visible;
  }
  span {
    position: relative;
    z-index: 1;
    overflow: hidden;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: transparent;
    bottom: 0;
    padding-top: 15%;
    margin-top: -15%;
    background-size: cover;
  }
  &::after {
    bottom: 0;
  }
}

.nice-to-meet {

  margin-bottom: 40px;

  .swiper-container {
    width: 100%;
    width: calc(100% + 20px);
    margin-left: -10px;
    height: 330px;

    @include breakpoint($lttablet) {
      height: auto;
    }

    .swiper-wrapper {
      @include breakpoint($lttablet) {
        display: block;
      }
      @include breakpoint($lttablet) {
        font-size: 0;
        text-align: center;
      }
    }

    .swiper-slide {
      width: 200px;
      transition: transform 0.3s ease, opacity 0.3s ease;
      transform: scale(0.85);
      opacity: 0.7;
      @include breakpoint($lttablet) {
        transform: scale(1);
        margin-bottom: 10px;
        opacity: 1;
      }

      @include breakpoint($lttablet) {
        transform: translateX(0);
        @include span(4 of 12);
        &:nth-child(3n) {
          @include last;
          float: none;
        }
        &:nth-child(4n) {
          clear: left;
        }

        display: inline-block;
        float: none;
        vertical-align: top;
        font-size: 17px;
      }

      @include breakpoint($ltdesktop) {
        font-size: 20px;
      }

      p {
        margin: 0;
      }
    }
    .swiper-slide-active {
      transform: scale(1);
      opacity: 1;
    }

    .swiper-pagination {
      @include breakpoint($lttablet) {
        display: none!important;
      }
    }
    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
    }

    .swiper-pagination-bullet-active {
      background-color: $primary-color;
    }
  }
}
